window.updateCountry = function(countryCode) {
    if (countryCode != "") {
        selectedCountry = countryCode;
        highlightCountry(chart, countryCode);
    }
}

function drawScatterLines() { 
  
    $(".dotLine").remove();

    var svg
    var lineFunction = d3.line()
      .x(function(d) { return d.x; })
      .y(function(d) { return d.y; })

    for (var i = 0; i < data.length; i++) {

        var iterator = 0;
        var xSaved = 0;
        var circleCoords = [];
        var line = d3.line()
        var nodes = d3.selectAll(".bb-circle-"+i)
        .each(function(d) {

            var item = d3.select(this);
            var visible = displayed.indexOf(d.id) !== -1;

            if (d.value != null && d.value != 0 && visible) {
                var xPadding = window.innerWidth < 600 ? 3 : 7;
                var yPadding = window.innerWidth < 600 ? 3 : 7;
                var cx = $(this).attr('cx') ? $(this).attr('cx') : parseFloat($(this).attr('x'))+xPadding;

                var cy = $(this).attr('cy') ? $(this).attr('cy') : parseFloat($(this).attr('y'))+yPadding;
                circleCoords.push({x:cx, y:cy});
            }
            if (indicators[currentCategory].removeZeroValue && d.value == 0) {
                this.remove();
            }
            if (d.id == "undefined" ) {
                this.remove();
            }
            iterator++;
        });

        svg = d3.selectAll("#chart svg .bb-chart-lines")

        var lineGraph = svg.append("path")
        .attr("d", lineFunction(circleCoords))
        .attr("class", "dotLine")
        .moveToBack();

    }
                
}

function highlightCountry(chart, code) {
    selectedCountry = code;
    var countryFound = false;
    var index = 0;

    d3.selectAll('g.bb-axis.bb-axis-x .tick') //here's how you get all the nodes
        .each(function(d) {
            // your update code here as it was in your example
            if (d3.select(this).selectAll("tspan").text() == _(code)) {
                d3.select(this).selectAll("text").attr("fill", "#E34A21");
                d3.select(this).selectAll("text").attr("font-weight", "bold");

                countryFound = true;
            } else {
                d3.select(this).selectAll("text").attr("fill", "black")
                d3.select(this).selectAll("text").attr("font-weight", "regular")
            }
            index++;
        });

    if (countryFound == false && parent) {
        parent.displayChartAlert("noData", code);
    } else {
        //alert('Data not available');
    }

    chart.load({
        rows: data
    });


}

